import React from 'react'
import { ModalProps } from 'antd/lib/modal'
import { length, __, gt, keys, map, pathOr } from 'ramda'
import Modal from '@/components/modal'
import { Snippets } from '@/constants/types'
import { renderSnippets } from '@/components/snippet-utils'
const { compose } = require('ramda')

interface Props extends ModalProps {
  snippetList?: Snippets
}

export default function SnippetPreviewModal({ onCancel, snippetList }: Props) {
  return (
    <Modal
      visible
      style={{ top: 30 }}
      maskClosable={false}
      className="ant-modal-header--border-none"
      keyboard={false}
      width="85%"
      onCancel={onCancel}
      footer={false}>
      {compose(
        map((key: string) => (
          <div key={key}>
            {compose(gt(__, 0), length, pathOr([], [key]))(snippetList) && (
              <h2 className="my-2">{key}</h2>
            )}
            {compose(renderSnippets(), pathOr([], [key]))(snippetList)}
          </div>
        )),
        keys
      )(snippetList)}
    </Modal>
  )
}
