import React from 'react'
import { Row, Col, Form, message } from 'antd'
import { PlusOutlined, MinusOutlined } from '@ant-design/icons'
import { RouteComponentProps } from 'react-router-dom'
import { keys, map, mergeAll, pathOr, replace } from 'ramda'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import Input from '@/components/input'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import { useReportPreview } from '@/data/useReportTemplate'
import Tabs from '@/components/draggable-tabs'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetListMenu from '@/components/snippet-list-menu'
import SnippetDragLayer from '@/components/snippet-drag-layer'
import SnippetsContainer from '@/components/snippets-container'
import SnippetPreviewModal from '@/components/snippet-preview-modal'
import Collapse from '@/components/collapse'
import {
  useReportTemplate,
  useAddNewTemplate,
  useUpdateTemplate,
} from '@/services/reportTemplate'
import history from '@/utils/history'
import { REPORT_TEMPLATE_EDIT } from '@/constants/routes'
const { compose } = require('ramda')

interface Props extends RouteComponentProps<{ id: string }> {}

function ReportTemplateBuilder(props: Props) {
  useTitle('結構式報告模版')

  useBreadcrumb([{ name: '報告模版' }, { name: '結構式報告模版' }])

  const { params } = props.match

  const {
    category,
    reportTemplate,
    snippetList,
    handleTabChange,
    handleTabEdit,
    handleTabMove,
    handleTabNameChange,
    handleSnippetUpdate,
  } = useReportTemplate({ id: params.id })

  React.useEffect(() => {
    form.setFieldsValue(reportTemplate)
  }, [JSON.stringify(reportTemplate)]) // eslint-disable-line

  const [isPreview, handlePreview] = useReportPreview()

  const [form] = Form.useForm()

  const [, handleAdd] = useAddNewTemplate({
    onCompleted: ({ id }) => {
      history.push(replace(':id', id)(REPORT_TEMPLATE_EDIT))
      message.success('報告模版已建立成功')
    },
  })

  const [, handleUpdate] = useUpdateTemplate({
    onCompleted: () => message.success('報告模版已儲存成功'),
  })

  const handleSave = () => {
    form.validateFields().then(values => {
      let handler = reportTemplate?.id ? handleUpdate : handleAdd

      handler({
        ...reportTemplate,
        ...values,
        setting: compose(
          mergeAll,
          map((key: string) => ({
            [key]: compose(
              map(({ Component, icon, ...x }) => ({
                ...x,
                categoryName: key,
              })),
              pathOr([], [key])
            )(snippetList),
          })),
          keys
        )(snippetList),
      })
    })
  }

  useHeaderContent(
    <div className="inline-flex">
      <Button
        color="outline"
        className="bg-transparent text-white border-white">
        發佈
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handleSave}>
        儲存
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handlePreview}>
        預覽
      </Button>
    </div>
  )

  return (
    <ReactDndProvider>
      {isPreview && (
        <SnippetPreviewModal
          snippetList={snippetList}
          onCancel={handlePreview}
        />
      )}
      <SnippetDragLayer />
      <Page>
        <Form form={form} initialValues={{ ...reportTemplate }}>
          <Row gutter={48} className="pt-2 relative">
            <Col span={5}>
              <Collapse
                defaultActiveKey={[1, 2]}
                expandIconPosition="right"
                className="shadow-2xl header-pl-clear"
                expandIcon={({ isActive }) =>
                  isActive ? <MinusOutlined /> : <PlusOutlined />
                }>
                <Collapse.Panel header="模版資訊" key={1}>
                  <Row className="mb-2 text-sm text-darkGrey">
                    <Col span={24} className="mb-3">
                      <div className="inline-block" css={{ width: 60 }}>
                        名稱:
                      </div>
                      <Form.Item noStyle name="name">
                        <Input className="w-2/3" />
                      </Form.Item>
                    </Col>
                    <Col span={24} className="my-3">
                      <div className="inline-block" css={{ width: 60 }}>
                        描述:
                      </div>
                      <Form.Item noStyle name="description">
                        <Input className="w-2/3" />
                      </Form.Item>
                    </Col>
                    <Col span={24} className="my-3">
                      <div className="inline-block" css={{ width: 60 }}>
                        創作者:
                      </div>
                      <Form.Item noStyle name="author">
                        <Input className="w-2/3" />
                      </Form.Item>
                    </Col>
                  </Row>
                </Collapse.Panel>
                <Collapse.Panel header="工具列" key={2}>
                  <Row>
                    <Col span={24}>
                      <SnippetListMenu />
                    </Col>
                  </Row>
                </Collapse.Panel>
              </Collapse>
            </Col>
            <Tabs
              type="editable-card"
              activeKey={category}
              onChange={handleTabChange}
              onEdit={handleTabEdit}
              moveTabNode={handleTabMove}
              className="flex-1">
              {compose(
                map((key: string) => (
                  <Tabs.TabPane
                    closable
                    key={key}
                    tab={
                      <span
                        contentEditable
                        suppressContentEditableWarning
                        spellCheck={false}
                        className="outline-none"
                        css={{ whiteSpace: 'nowrap' }}
                        onChange={handleTabNameChange(key)}
                        // onBlur={handleTabNameChange(key)}
                      >
                        {key}
                      </span>
                    }>
                    <Col
                      span={19}
                      className="report-template-builder__main-content">
                      <SnippetsContainer
                        draggable={!isPreview}
                        snippetList={snippetList[key]}
                        onSnippetUpdate={handleSnippetUpdate}
                      />
                    </Col>
                  </Tabs.TabPane>
                )),
                keys
              )(snippetList)}
            </Tabs>
          </Row>
        </Form>
      </Page>
    </ReactDndProvider>
  )
}

export default ReportTemplateBuilder
